<template>
  <base-card
    color="rgba(203, 170, 92, 0.65)"
    class="pa-2"
  >
    <v-container>
      <v-row class="pa-2">

        <v-responsive
          class="overflow-visible mr-2"
          max-width="500"
        >
          <base-subheading>Mini Converter Online </base-subheading>
          <p>Enter your Java Minecraft Username and optionally select a Helmet and Weapon to weild!</p>

          <v-text-field
            label="Enter Minecraft username"
            v-model="username"
          ></v-text-field>

          <div>
            <v-icon
              v-if="selectedHelmet"
              @click="selectedHelmet = ''"
              style="float: right; transform: translate(0%, 100%);"
              small
            >
              $delete
            </v-icon>
            <v-combobox
              label="Helmet (optional)"
              v-model="selectedHelmet"
              :items="helmets"
            >
            </v-combobox>
          </div>

          <div>
            <v-icon
              v-if="selectedWeapon"
              @click="selectedWeapon = ''"
              style="float: right; transform: translate(0%, 100%);"
              small
            >
              $delete
            </v-icon>
            <v-combobox
              label="Weapon (optional)"
              v-model="selectedWeapon"
              :items="weapons"
            >
              <template v-slot:selection="data">
                <v-chip
                  :key="JSON.stringify(data.item)"
                  v-bind="data.attrs"
                  :input-value="data.selected"
                  :disabled="data.disabled"
                  @click:close="data.parent.selectItem(data.item)"
                >
                  <v-avatar
                    size="36px"
                  >
                    <img
                      alt="Avatar"
                      :src="getImage(data.item.id)"
                    >
                  </v-avatar>
                  &nbsp;{{ data.item.text }}
                </v-chip>
              </template>

              <template v-slot:item="{ item }">
                <v-avatar
                  size="36px"
                >
                  <img
                    alt="Avatar"
                    :src="getImage(item.id)"
                  >
                </v-avatar>
                &nbsp;
                {{ item.text }}
              </template>
            </v-combobox>
          </div>
          
          <v-checkbox
            label="Wearing a Hoodie?"
            v-model="forceHoodie"
          >
          </v-checkbox>

          <v-checkbox
            label="Enchant Helmet and or Weapon?"
            v-model="enchantItems"
          >
          </v-checkbox>

          <v-btn
            :block="$vuetify.breakpoint.xsOnly"
            class="ma-0"
            color="secondary"
            style="height: 48px"
            :disabled="isLoading"
            @click="getMini"
          >
            {{ buttonText }}

            <moon-loader :loading="isLoading" color="#000" size="24px"></moon-loader>
          </v-btn>

          <div>
            <h4 style="color: #f00">{{ errorMessage }}</h4>
          </div>

        </v-responsive>

        <div style="position: relative">
          <moon-loader :loading="isLoading" color="#083759" size="250px" 
            style="position: absolute; z-index: 1; transform: translate(50%, 50%); -ms-transform: translate(50%, 50%);"></moon-loader>
          <v-img
            max-width="474px"
            max-height="474px"
            ref="image"
            :src="generatedImage"
          />
        </div>
          <v-btn
            :block="$vuetify.breakpoint.xsOnly"
            class="ma-0"
            color="secondary"
            v-if="searchedPerformed"
            @click="downloadMini"
          >
            Save
          </v-btn>
      </v-row>
    </v-container>
  </base-card>
</template>

<script>
  import axios from 'axios';
  import MoonLoader from 'vue-spinner/src/MoonLoader.vue'

  export default {
    name: 'MiniGenerator',

    components: {
      MoonLoader
    },

    data () {
      return {
        username: '',
        selectedHelmet: '',
        selectedWeapon: '',
        forceHoodie: false,
        enchantItems: false,

        helmets: [ '' ],
        weapons: [ '' ],

        buttonText: '',
        generatedImage: '',
        isLoading: false,
        errorMessage: '',
        searchedPerformed: false
      }
    },

    methods: {
      getMini() {
        if (this.username == '') {
          this.errorMessage = 'Enter a username';
          return;
        }

        this.isLoading = true;
        this.errorMessage = '';
        this.buttonText = 'Making Mini ... ';
        this.searchedPerformed = false;
        var addHelmet = this.selectedHelmet != '';
        var addWeapon = this.selectedWeapon != '';

        const config = { url: 'https://k2kvqkgw5k.execute-api.eu-west-2.amazonaws.com/api/mini', method: "post", responseType: "blob", data: {
            username: this.username,
            addHelmet: addHelmet,
            helmet: this.selectedHelmet.id,
            addWeapon: addWeapon,
            weapon: this.selectedWeapon.id,
            forceHoodie: this.forceHoodie,
            enchant: this.enchantItems
        } };

        axios.request(config)
          .then(response => {
            this.rawMiniData = response.data;
            this.generatedImage = URL.createObjectURL(response.data);
            this.searchedPerformed = true;
          })
          .catch(err => {
            debugger;
            new Promise((resolve, reject) => {
              let reader = new FileReader();
              reader.onload = () => {
                err.response.data = JSON.parse(reader.result);
                resolve(err);
              };

              if (err.response) {
                reader.readAsText(err.response.data);
              }
            })
            .then(err => {
              console.log(err.response.data);
              if (err.response.status == 429) {
                this.errorMessage = 'Service is very busy at the moment, try again later ';
              } else {
                this.errorMessage = err.response.data.message;
              }
            })
          })
          .finally(() => {
            this.isLoading = false;
            this.buttonText = 'Make Mini';
          });
      },
      downloadMini() {
        const link = document.createElement("a");

        // Set link's href to point to the Blob URL
        link.href = this.generatedImage;
        link.download = `${this.username}-mini.png`;

        // Append link to the body
        document.body.appendChild(link);

        // Dispatch click event on the link
        // This is necessary as link.click() does not work on the latest firefox
        link.dispatchEvent(
          new MouseEvent('click', { 
            bubbles: true, 
            cancelable: true, 
            view: window 
          })
        );

        // Remove link from body
        document.body.removeChild(link);
      },
      titleCase(str) {
        return str
          .split(' ')
          .map((word) => word[0].toUpperCase() + word.slice(1).toLowerCase())
          .join(' ');
      },
      setDefaultImage() {
        this.generatedImage = require(`@/assets/minis/mrjoshuat-b90b0662-208d-43aa-fa2f-08d93be575f2.png`);
      },
      getConfigItems() {
        axios
          .get('https://k2kvqkgw5k.execute-api.eu-west-2.amazonaws.com/api/config')
          .then(response => {
            this.helmets = response.data.helmets.map(_ => {
              return {
                id: _,
                text: this.titleCase(_).replaceAll('_', ' ')
              };
            });
            this.weapons = response.data.weapons.map(_ => {
              return {
                id: _,
                text: this.titleCase(_).replaceAll('_', ' ')
              };
            });
          });
      },
      getImage(id) {
        return require(`@/assets/weapons/${id}.png`);
      }
    },

    mounted() {
      this.buttonText = 'Make Mini';

      this.getConfigItems();
      this.setDefaultImage();
    }
  }
</script>
